import { useState, useEffect } from "react";

import MovieDetail from "../MovieDetail/MovieDetail";

import "./MovieList.css";

const IMG_URL = `https://image.tmdb.org/t/p/original`;

const MovieList = (props) => {
  //State qun l trng thi d liu phim c tr v 
  const [movies, setMovies] = useState([]);

  //State qun l thng tin phim c click
  const [movieData, setMovieData] = useState({});

  //State qun l vic n hin thng tin ca phim c click
  const [showMovieDetail, setShowMovieDetail] = useState(false);

  //State qun l id phim c click 
  const [clickedMoiveId, setClickedMovieId] = useState(null);

  //S dng useEffect  hm fetchMoivedData chy li mi khi requestType thay i
  useEffect(() => {
    const fetchMoivedData = async () => {
      try {
        const response = await fetch(props.requestType);
        // console.log(response);
        // Nu status ca response not ok (false)
        if (!response.ok) {
          throw new Error("Something went wrong!");
        }

        const data = await response.json();
        // console.log(data.results);
        setMovies(data.results);
      } catch (error) {
        console.log(error.message);
      }
    };
    fetchMoivedData();
  }, [props.requestType]);

  return (
    <div className="movieList">
      <h2 className="movieList_title">{props.requestName}</h2>
      <div className="movieList_images scrollbar">
        {movies.map((movie) => (
          <img
            key={movie.id}
            //phim thuc danh mc Original (requestName="") s c hin th di dng Poster
            src={
              props.requestName === ""
                ? IMG_URL + movie.poster_path
                : IMG_URL + movie.backdrop_path
            }
            className={`movieList_image ${
              props.requestName === "" && "movieList_imagePoster"
            }`}
            alt={movie.name}
            onClick={() => {
              setMovieData({
                movie_id: movie.id,
                movie_name: movie.name || movie.title,
                movie_image: movie.backdrop_path,
                vote_average: movie.vote_average,
                release_date: movie.first_air_date || movie.release_date,
                overview: movie.overview || "This moive has no overview",
              });

              // So snh id ca phim trc v sau khi click
              if (clickedMoiveId === movie.id) {
                setShowMovieDetail(!showMovieDetail); // n hin thng tin phim c click
              } else {
                setShowMovieDetail(true);
              }
              setClickedMovieId(movie.id); // Lu id ca phim c click
            }}
          />
        ))}
      </div>
      {/*n hin thng tin phim c click */}
      {showMovieDetail && <MovieDetail movieData={movieData} />}
    </div>
  );
};

export default MovieList;
